<template>
  <div class="page-blog-edit">
      <div class="breadcrumb">
      <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{path:'/' }"></el-breadcrumb-item>
          <el-breadcrumb-item :to="{name:'blogList'}"></el-breadcrumb-item>
          <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="editor">
      <div class="form">
        <el-form>
          <el-form-item label="">
              <el-input v-model="blog.title" :maxlength="30"></el-input>
          </el-form-item>
          <el-form-item label="">
              <el-input v-model="blog.author" :maxlength="30"></el-input>
          </el-form-item>
          <el-form-item label="">
              <el-input v-model="blog.abstract" :maxlength="100"></el-input>
          </el-form-item>
          <el-form-item>
              <div ref="editor"></div>
          </el-form-item>
          <el-form-item>
            <el-button v-if="id" @click="saveBtnClick"></el-button>
            <el-button v-else @click="createBtnClick"></el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="preview">
          <p class="tag"></p>
          <h2 class="title">{{blog.title}}</h2>
          <div class="author"> / {{blog.author}}</div>
          <div class="abstract">{{blog.abstract}}</div>
          <div class="content blog-content" v-html="blog.content"></div>
      </div>
    </div>
  </div>
</template>
<script>
import E from 'wangeditor'
export default {
  data(){
    return {
       id:this.$route.query.id,
       editor:null,
       blog:{
         title:'',
         author:'',
         abstract:'',
         content:''
       }
    }
  },
  mounted(){
    this.initEditor();
  },
  methods:{
    //
    async getBlog(){
      let req = {id:this.id};
      let res = await this.$api.getBlogById(req);
      if(res.code == 200){
        this.blog = res.data||{};
        this.editor.txt.html(this.blog.content);//
      }else{
        this.$message.error("");
      }
    },
    //
    initEditor(){
      this.$nextTick(()=>{
        this.editor = new E(this.$refs.editor);
        this.editor.customConfig.menus = ['bold','italic','underline','link','image','code'];
        this.editor.customConfig.zIndex = 0;
        this.editor.customConfig.onchange = (html)=>{
            // html 
            this.blog.content = html;
        }
        this.editor.create();
        //id
        if(this.id){
          this.getBlog();
        }
      })
    },
    //
    validateForm(){
      //
       if(this.blog.title==''){
         this.$message.warning('');
         return false;
       }
       if(this.blog.author==''){
         this.$message.warning('');
         return false;
       }
       if(this.blog.abstract==''){
         this.$message.warning('');
         return false;
       }
       return true;
    },
    //
    async saveBtnClick(){
       this.blog.content = this.editor.txt.html();
       if(!this.validateForm()){
          return false;
       }
       //
      let res = await this.$api.updateBlog(this.blog);
      if(res.code == 200){
        this.$message.success('');
        this.$router.back();
      }else{
        this.$message.error('');
      }
    },
    //
    async createBtnClick(){
        this.blog.content = this.editor.txt.html();
       if(!this.validateForm()){
          return false;
       }
       //
      let res = await this.$api.createBlog(this.blog);
      if(res.code == 200){
        this.$message.success('');
        this.$router.back();
      }else{
        this.$message.error('');
      }
    }
  }
}
</script>
<style>
.blog-content p,.blog-content div{
  margin: 15px 0;
}
.blog-content img{
  max-width: 100%;
  display: inline-block;
  margin: 0 auto;
}
.blog-content pre{
  padding: 15px;
  display: block;
  background: #EEE;
  overflow: auto;
  border:1px solid #CCC;
}
</style>

<style lang="less" scoped>
.page-blog-edit{
   >.editor{
     padding: 30px;
     background: #FFF;
     display: flex;
     .form{
       width: 500px;
     }
     .preview{
       flex: 1;
       width: 0;
       background: #FFF;
       padding: 0 30px;
       .tag{
         background: rgba(64,158,255,.1);
         border: 1px solid rgba(64,158,255,.2);
         color: #409eff;
         padding: 5px 10px;
       }
       .title{
         font-size: 20px;
         font-weight: normal;
         margin-top: 10px;
       }
       .author{
         margin-top: 20px;
         color: #999;
       }
       .abstract{
         background: #EEE;
         padding: 30px;
         margin-top: 20px;
         color: #666;
         line-height: 26px;
       }
       .content{
         margin-top: 20px;
         word-break:break-all;
       }
     }
   }
}
</style>





